.modal {
    height: 100vh;
    width: 100vw;
    top:0;
    left:0;
    background-color: rgba(28, 26, 26, 0.4);
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    transform: scale(0);
    opacity: 0;
    transition: 0.5s;
    pointer-events: none;
    z-index: 100;
}


.modal.active {
    opacity: 1;
    pointer-events: all;
    transform: scale(1);
}


.modalContent {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    width: 50vw;
    transform: scale(0.5);
    transition: 0.4s all;
}

.modalContent.active {
    transform: scale(1);
}

